.register{
    width: 400px;
    background: #fff;
    border: 1px solid #ffffe2;
    box-shadow: 0 2px 4px rgb(0 0 0 /10%), 0 8px 16px rgb(0 0 0 /10%);
    border-radius: 8px;
    padding: 1rem;
    align-items: center;
    text-align: center;
}

.register > input{
    border-radius: 8px;
    border: 2px solid #dddfe2;
    outline: none;
    color: #1d2129;
    margin: 0.5rem 0.75rem;
    width: 92%;
    font-size: 1rem;
}

.register > .button{
    background: #1d2a3d;
    border: 1px solid #1d2a3d;
    color: #fff;
    font-size: 1.5rem;
    padding: 0.5rem;
    margin: 0.5rem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
}